import ROUTES from '../constants/routes';

export const ROUTE_MAP = {
  flowNetwork: () => ROUTES.rfFlowNetwork,
  streams: flowNetworkId => ROUTES.rfFlowNetworkStreams.replace(':flowNetworkId', flowNetworkId),
  producers: (flowNetworkId, streamId) =>
    ROUTES.rfFlowNetworkProducer.replace(':flowNetworkId', flowNetworkId).replace(':streamId', streamId),
  writersClones: (flowNetworkId, streamId, producerId) =>
    ROUTES.rfFlowNetworkWriterClone
      .replace(':flowNetworkId', flowNetworkId)
      .replace(':streamId', streamId)
      .replace(':producerId', producerId),
  flowNetworkClone: () => ROUTES.rfFlowNetworkClone,
  streamClones: flowNetworkId => ROUTES.rfFlowNetworkStreamsClone.replace(':flowNetworkId', flowNetworkId),
  consumers: (flowNetworkId, streamId) =>
    ROUTES.rfFlowNetworkCloneConsumers.replace(':flowNetworkId', flowNetworkId).replace(':streamId', streamId),
  writers: (flowNetworkId, streamId, consumerId) =>
    ROUTES.rfWriters
      .replace(':flowNetworkId', flowNetworkId)
      .replace(':streamId', streamId)
      .replace(':consumerId', consumerId),
};
