<template>
  <flow-network-navigation-ui
    :search="search"
    :updateSearch="updateSearch"
    :currentUrl="currentUrl"
    :routeLinks="routeLinks"
    :isFetchingData="isFetchingData"
    :fetchFlowNetworks="fetchFlowNetworks"
  ></flow-network-navigation-ui>
</template>
<script>
import FlowNetworkNavigationUi from './flowNetworkNavigationUi.vue';
import { rfFlowNetworkService } from '../../../services/rfFlowNetworkService';
import { ROUTE_MAP } from '../../../uiservice/flowNetwork';
export default {
  data: () => ({
    routeLinks: [],
    currentUrl: '',
    flowNetworks: [],
    isFetchingData: false,
  }),
  components: { FlowNetworkNavigationUi },
  mounted() {
    this.fetchFlowNetworks();
  },
  computed: {
    search() {
      return this.$store.state.flowNetworkSearch;
    },
  },
  watch: {
    '$route.path': {
      handler: function (path) {
        this.currentUrl = path;
      },
      deep: true,
      immediate: true,
    },
  },
  methods: {
    updateSearch(searchValue) {
      this.$store.commit('updateFlowNetworkSearch', searchValue);
    },
    updateNavigationNames(flowNetworks) {
      this.$store.commit('updateFlowNetworkNavigationNames', flowNetworks)
    },
    fetchFlowNetworks() {
      this.isFetchingData = true;
      rfFlowNetworkService
        .fetch({ with_producers: true, with_streams: true })
        .then(data => {
          this.flowNetworks = data;
          this.updateNavigationNames(this.flowNetworks);
          this.constructRouteLinks();
        })
        .finally(() => {
          this.isFetchingData = false;
        });
    },
    routeLink(id, name, prefix) {
      return {
        id: id,
        name: `${prefix} ${name}`,
      };
    },
    flowNetworkLink() {
      return ROUTE_MAP.flowNetwork();
    },
    flowNetworkStreamsLink(flowNetworkId) {
      return ROUTE_MAP.streams(flowNetworkId);
    },
    streamsProducerLink(flowNetworkId, streamId) {
      return ROUTE_MAP.producers(flowNetworkId, streamId);
    },
    producerWriterCloneLink(flowNetworkId, streamId, producerId) {
      return ROUTE_MAP.writersClones(flowNetworkId, streamId, producerId);
    },
    constructRouteLinks() {
      this.routeLinks = [
        {
          id: 1,
          name: 'All Flow Networks',
          type: 'network',
          link: this.flowNetworkLink(),
          children: this.getTreeViewData(this.flowNetworks),
          count: this.flowNetworks.length,
        },
      ];
    },
    getTreeViewData(flowNetworks) {
      return flowNetworks.map(flowNetwork => ({
        ...this.routeLink(flowNetwork.uuid, flowNetwork.name, 'Flow N/W:'),
        link: this.flowNetworkStreamsLink(flowNetwork.uuid),
        type: 'flowNetworks',
        count: flowNetwork.streams && flowNetwork.streams.length,
        children:
          flowNetwork.streams &&
          flowNetwork.streams.map(stream => ({
            ...this.routeLink(stream.uuid, stream.name, 'Stream:'),
            type: 'writer',
            link: this.streamsProducerLink(flowNetwork.uuid, stream.uuid),
            count: stream.producers && stream.producers.length,
            children:
              stream.producers &&
              stream.producers.map(producer => ({
                ...this.routeLink(producer.uuid, producer.name, 'Producer:'),
                type: 'writerClones',
                count: producer.writer_clones && producer.writer_clones.length,
                link: this.producerWriterCloneLink(flowNetwork.uuid, stream.uuid, producer.uuid),
              })),
          })),
      }));
    },
  },
};
</script>
<style>
.network-nav .text-style {
  text-decoration: unset;
  color: unset;
  font-size: 0.8125rem;
  line-height: 1rem;
}

.full-height {
  height: 100%;
}

.active-item {
  font-weight: bold !important;
  font-size: 1rem !important;
}

.navigation-item-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navigation-wrapper {
  border-right: 1px solid #333;
  padding: 0.5em;
}
</style>


